<template>
  <div class="operate-task-log">
    <div class="topTitle">
      <span class="topTitle-span1">过程管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">过程内容</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">冻结涉及恐怖活动信息</span>
      <div class="button-content">
        <Button
          type="primary"
          @click="addfun('addFlag')"
          v-authcontrol="{menuCode:'108001002003001'}"
          v-if="firstLoginFlag"
        >
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <Button
          type="primary"
          v-authcontrol="{menuCode:'108001002003004'}"
          v-if="firstLoginFlag"
          @click="exportFn"
        >
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :label-width="120" inline>
            <Row>
              <!-- <FormItem label="协查事项：" prop>
                <Input v-model="formValidate.val1" placeholder="请输入" style="width:200px"></Input>
              </FormItem>-->
              <FormItem label="管理部门：" prop="extraUnit">
                <treeselect
                  v-model="formValidate.val9"
                  valueConsistsOf="ALL" 
              :limit="3"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择"
                  :max-height="100"
                  style="width:200px"
                />
              </FormItem>
              <FormItem label="执行机构：" prop>
                <Input v-model="formValidate.val2" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="执行省份：" prop>
                <Select v-model="formValidate.val3" style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    v-for="(item,index) in regionArr"
                    :key="index"
                    :value="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
            </Row>
            <Row>
              <FormItem label="执行人：" prop>
                <Input v-model="formValidate.val4" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="协查日期：">
                <DatePicker
                  @on-change="datechange"
                  :value="formValidate.val5"
                  format="yyyy-MM-dd"
                  type="daterange"
                  placement="bottom-end"
                  placeholder="请选择时间"
                  style="width: 200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="被调查人客户名称：" prop>
                <Input v-model="formValidate.val6" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <FormItem label="业务名称：" prop>
                <Input v-model="formValidate.val7" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="状态：" prop>
                <Select v-model="formValidate.val8" filterable style="width:200px">
                  <Option value>请选择</Option>
                  <!-- PROCESS_CHECK_STATE -->
                  <Option
                    v-for="(item,index) in PROCESS_CHECK_STATE"
                    :key="index"
                    :value="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
              <!-- <FormItem label="管理部门：" prop="extraUnit">
                <treeselect
                  v-model="formValidate.val9"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择"
                  :max-height="100"
                  style="width:200px"
                />
              </FormItem>-->
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('formValidate','0')">查询</Button>
                <Button type="primary" @click="handleSubmit('formValidate','1')">精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td style="width:100px">
                    <span
                      class="spanBox"
                      v-authcontrol="{menuCode:'108001002003002'}"
                      v-if="firstLoginFlag"
                      @click="addfun('modifyFlag',item.busId,item.id,item.extraCode)"
                    >修改</span>
                    <span
                      class="spanBox"
                      @click="delItem(item.id,item.extraCode)"
                      v-authcontrol="{menuCode:'108001002003003'}"
                      v-if="firstLoginFlag"
                    >删除</span>
                    <span
                      class="spanBox"
                      v-authcontrol="{menuCode:'108001002003007'}"
                      v-if="firstLoginFlag"
                      @click="addfun('appendFlag',item.busId,item.id,item.flag)"
                    >追加</span>
                  </td>
                  <td
                    class="spanBox"
                    @click="watchTableCellFun(item,item.busId,item.id)"
                  >{{item.assistWork}}</td>
                  <td>{{item.agent}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.executeOrg"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.executeOrg}}</span>
                    </el-popover>
                  </td>
                  <td>{{item.executeProvince}}</td>
                  <td>{{item.executor}}</td>
                  <td>{{item.executorId}}</td>
                  <td>{{item.agentTime}}</td>
                  <td>{{item.respondent}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.caseType"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.caseType}}</span>
                    </el-popover>
                  </td>
                  <td>{{item.involvedMoney}}</td>
                  <td>{{item.exchangeNum}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.businessName"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.businessName}}</span>
                    </el-popover>
                  </td>
                  <td class="spanBox" @click="showProcess(item)">{{item.flag}}</td>
                  <td>{{item.ifShow=='true'?'是':'否'}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.content"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.content}}</p>
                    </el-popover>
                  </td>
                  <td class="spanBox" @click="watchFileFun(item.busId,item.id,item)">查看</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.manager"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.manager}}</p>
                    </el-popover>
                  </td>
                  <td class="spanBox" @click="watchAppendFun(item,item.busId,item.id)">查看</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.createTime"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.createTime}}</p>
                    </el-popover>
                  </td>
                  <td>{{item.creator}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      title
                      width="200"
                      trigger="hover"
                      :content="item.updateTime"
                    >
                      <p slot="reference" class="textOverflow textMaxWidth">{{item.updateTime}}</p>
                    </el-popover>
                  </td>
                  <td>{{item.updater}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page
            :total="total"
            :current.sync="pageNum"
            show-elevator
            show-sizer
            @on-change="pageChangeFun"
            @on-page-size-change="pageSizeChange"
          ></Page>
        </div>
      </div>
    </div>
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal1" width="1000" class="dialog" title="设置">
      <div style="height:400px;overflow-y: auto;" class="modelSonDiv">
        <Form
          ref="formValidateModel1"
          :model="formValidateModel1"
          :rules="ruleValidate"
          :label-width="120"
        >
          <FormItem label="流程标题" prop="bpmTitle">
            <Input v-model="formValidateModel1.bpmTitle" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="协助事项" prop="val1">
            <CheckboxGroup v-model="formValidateModel1.val1">
              <Checkbox
                v-for="(item,index) in PROCESS_ASSIST_ITEM"
                :key="index"
                :disabled="addFlag"
                :label="item.code"
              >{{item.name}}</Checkbox>
            </CheckboxGroup>
          </FormItem>
          <FormItem label="经办人" prop="val2">
            <Input v-model="formValidateModel1.val2" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="执行机构" prop="val3">
            <Input v-model="formValidateModel1.val3" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="执行省份" prop="val4">
            <Select v-model="formValidateModel1.val4" :disabled="this.addFlag">
              <Option value>请选择</Option>
              <Option
                v-for="(item,index) in regionArr"
                :key="index"
                :value="item.code"
                :disabled="addFlag"
              >{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="执行人" prop="val5">
            <Input v-model="formValidateModel1.val5" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="执行人证件号码" prop="val6">
            <Input v-model="formValidateModel1.val6" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="协查日期" prop="val7">
            <DatePicker
              type="date"
              :value="formValidateModel1.val7"
              placeholder="请输入时间"
              style="width:100%"
              :disabled="this.addFlag"
              @on-change="datechange2"
            ></DatePicker>
          </FormItem>
          <FormItem label="被调查人">
            <!-- ============== -->
            <Row>
              <Row style="padding-bottom:10px;padding-right:10px; box-sizing: border-box;">
                <Button
                  type="dashed"
                  icon="md-add"
                  size="small"
                  style="float:right"
                  @click="addpeoplefun"
                  :disabled="addFlag"
                >新增</Button>
              </Row>
              <Row class="flexStart">
                <!-- <Row class="processAddPeople1">被调查人</Row> -->
                <Row class>
                  <Row
                    v-for="(item,index) in formValidateModel1.addPeopleArr"
                    class="flexStart"
                    :key="index"
                    style="border:1px dashed #E8EAEC"
                  >
                    <Row style="width:70%" class="flexStartWrap">
                      <Row class="processAddPeople-cell-cell">
                        <!-- <p class="processAddPeople3">客户号</p> -->
                        <!-- <Input
                          v-model="item.clientId"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />-->
                        <FormItem label="客户号" prop="d1" :label-width="100">
                          <Input
                            v-model="item.clientId"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </FormItem>
                      </Row>
                      <Row class="processAddPeople-cell-cell">
                        <!-- <p class="processAddPeople3">客户名称</p>
                        <Input
                          v-model="item.clientName"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />-->
                        <FormItem label="客户名称" prop="d2" :label-width="100">
                          <Input
                            v-model="item.clientName"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </FormItem>
                      </Row>
                      <Row class="processAddPeople-cell-cell">
                        <!-- <p class="processAddPeople3">证件类型</p>
                        <Input
                          v-model="item.certificateType"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />-->
                        <FormItem label="证件类型" prop="d3" :label-width="100">
                          <Input
                            v-model="item.certificateType"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </FormItem>
                      </Row>
                      <Row class="processAddPeople-cell-cell">
                        <!-- <p class="processAddPeople3">证件名称</p>
                        <Input
                          v-model="item.certificateId"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />-->
                        <FormItem label="证件号码" prop="d4" :label-width="100">
                          <Input
                            v-model="item.certificateId"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </FormItem>
                      </Row>
                    </Row>
                    <Row style="width:30%;text-align: center;">
                      <Button
                        type="dashed"
                        icon="ios-close"
                        size="small"
                        style="float:right;margin-right:10px;margin-top:23px"
                        @click="delPeoplefun(index)"
                        :disabled="addFlag"
                      >删除</Button>
                    </Row>
                  </Row>
                </Row>
              </Row>
            </Row>
            <!-- ============== -->
          </FormItem>
          <!-- <FormItem label="涉及恐怖人员及组织" prop="">
            <Input v-model="formValidateModel1.val8" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>-->
          <FormItem label="案件类型" prop="val9">
            <Input v-model="formValidateModel1.val9" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="涉案金额" prop="val10">
            <Input v-model="formValidateModel1.val10" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="交易笔数" prop="val12">
            <Input v-model="formValidateModel1.val12" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="业务名称" prop="val13">
            <Input v-model="formValidateModel1.val13" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="是否公开">
            <i-switch v-model="formValidateModel1.val14" size="large" :disabled="this.addFlag">
              <span slot="true">On</span>
              <span slot="false">Off</span>
            </i-switch>
          </FormItem>
          <FormItem label="编辑内容" prop="val15">
            <Input
              v-model="formValidateModel1.val15"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入"
              :disabled="this.addFlag"
            ></Input>
          </FormItem>

          <FormItem label="上传附件">
            <showUsualFiles
              :file-obj="dataObj"
              :key="fileId"
              @requestUploadList="receiveUploadList"
            ></showUsualFiles>
          </FormItem>
          <Row v-if="addFlagShow">
            <Row style="text-again:left;border-bottom:1px dashed #E8EAEC;margin-bottom:20px">追加信息</Row>
            <FormItem label="上传附件">
              <showUsualFiles
                :file-obj="dataObj2"
                :key="fileId2"
                @requestUploadList="receiveUploadList2"
              ></showUsualFiles>
            </FormItem>
            <FormItem label="追加内容">
              <Input
                v-model="formValidateModel1.val11"
                type="textarea"
                :autosize="{minRows: 2,maxRows: 5}"
                placeholder="请输入"
              ></Input>
            </FormItem>
          </Row>
          <Modal :mask-closable="false" v-model="taskPeipleModel" width="360" class="treeBoxModel">
            <p slot="header" style="color:#f60;text-align:center">
              <span>审核人信息</span>
            </p>
            <div style="text-align:left;overflow-y:auto;height:300px">
              <!-- <FormItem prop="nextStepName" :label-width="10">
                <Input
                  type="textarea"
                  :autosize="{minRows: 2,maxRows: 5}"
                  v-model="formValidateModel1.nextStepName"
                  placeholder="请输入此次审核名称"
                  @on-blur="nextStepNameFun"
                ></Input>
              </FormItem>-->
              <FormItem prop="nextUser" :label-width="10">
                <Tree
                  ref="treeRef"
                  :data="data2"
                  show-checkbox
                  multiple
                  @on-check-change="updateNextUser"
                ></Tree>
              </FormItem>
            </div>
            <div slot="footer">
              <Button
                type="primary"
                size="large"
                long
                @click="handleSubmitLast('formValidateModel1')"
                :disabled="buttonDisabled"
              >立即申请</Button>
            </div>
          </Modal>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="save1('formValidateModel1')">{{buttonVal}}</Button>
          <!-- <Button @click="modal1=false">取消</Button> -->
          <Button @click="modal1=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchFileModel" width="90%" title="查看">
      <showUsualFiles :file-obj="dataObj3" :key="fileId3" @requestUploadList="receiveUploadList3"></showUsualFiles>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchTableCellDitalModel" width="90%" title="查看">
      <div class="content">
        <Form :label-width="120">
          <FormItem label="流程标题：">
            <label>{{watchTableCell.bpmTitle}}</label>
          </FormItem>
          <FormItem label="协助事项：">
            <label>{{watchTableCell.val1}}</label>
          </FormItem>
          <FormItem label="经办人：">
            <label>{{watchTableCell.val2}}</label>
          </FormItem>
          <FormItem label="执行机构：">
            <label>{{watchTableCell.val3}}</label>
          </FormItem>
          <FormItem label="执行省份：">
            <label>{{watchTableCell.val4}}</label>
          </FormItem>
          <FormItem label="执行人：">
            <label>{{watchTableCell.val5}}</label>
          </FormItem>
          <FormItem label="执行人证件号码：">
            <label>{{watchTableCell.val6}}</label>
          </FormItem>
          <FormItem label="协查日期：">
            <label>{{watchTableCell.val7}}</label>
          </FormItem>
          <FormItem label="被调查人：">
            <label>
              <Row>
                <Row class="flexStart">
                  <Row class>
                    <Row
                      v-for="(item,index) in watchTableCell.addPeopleArr"
                      class="flexStart"
                      :key="index"
                      style="border:1px dashed #E8EAEC"
                    >
                      <Row style="width:70%" class="flexStartWrap">
                        <Row class="flexCenter processAddPeople-cell-cell">
                          <p class="processAddPeople3">客户号</p>
                          <Input
                            v-model="item.clientId"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </Row>
                        <Row class="flexCenter processAddPeople-cell-cell">
                          <p class="processAddPeople3">客户名称</p>
                          <Input
                            v-model="item.clientName"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </Row>
                        <Row class="flexCenter processAddPeople-cell-cell">
                          <p class="processAddPeople3">证件类型</p>
                          <Input
                            v-model="item.certificateType"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </Row>
                        <Row class="flexCenter processAddPeople-cell-cell">
                          <p class="processAddPeople3">证件号码</p>
                          <Input
                            v-model="item.certificateId"
                            placeholder
                            style="width:50%"
                            size="small"
                            :disabled="addFlag"
                          />
                        </Row>
                      </Row>
                    </Row>
                  </Row>
                </Row>
              </Row>
            </label>
          </FormItem>
          <FormItem label="案件类型：">
            <label>{{watchTableCell.val9}}</label>
          </FormItem>
          <FormItem label="涉案金额：">
            <label>{{watchTableCell.val10}}</label>
          </FormItem>
          <FormItem label="交易笔数：">
            <label>{{watchTableCell.val12}}</label>
          </FormItem>
          <FormItem label="业务名称：">
            <label>{{watchTableCell.val13}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val14=="true"?'是':'否'}}</label>
          </FormItem>

          <FormItem label="编辑内容：">
            <label>
              <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="watchTableCell.val5"
              >
                <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val5}}</p>
              </el-popover>
            </label>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
              @requestUploadList="receiveUploadList4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
              @requestUploadList="receiveUploadList5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <label>
              <!-- {{watchTableCell.val11}} -->
              <el-popover
                placement="top-start"
                title
                width="200"
                trigger="hover"
                :content="watchTableCell.val11"
              >
                <p slot="reference" class="textMaxWidth2 textOverflow">{{watchTableCell.val11}}</p>
              </el-popover>
            </label>
          </FormItem>
        </Form>
      </div>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchAppenModel" width="99%" title="查看">
      <div class="content">
        <Row>
          <table class="tableBox">
            <thead>
              <tr>
                <td v-for="(item,index) in tableHeaderAppend">{{item.name}}</td>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item,index) in tableBodyAppend">
                <td>{{index+1}}</td>
                <!-- <el-popover
                  placement="top-start"
                  title
                  width="200"
                  trigger="hover"
                  :content="item.content"
                >
                  <td slot="reference" class="textOverflow textMaxWidth textTdTop">{{item.content}}</td>
                </el-popover>-->
                <td>
                  <el-popover
